/* roboto-regular - latin_cyrillic */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("/src/fonts/Roboto/roboto-v20-latin_cyrillic-regular.eot");
  /* IE9 Compat Modes */
  src: local(""),
    url("/src/fonts/Roboto/roboto-v20-latin_cyrillic-regular.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */
    url("/src/fonts/Roboto/roboto-v20-latin_cyrillic-regular.woff2") format("woff2"),
    /* Super Modern Browsers */
    url("/src/fonts/Roboto/roboto-v20-latin_cyrillic-regular.woff") format("woff"),
    /* Modern Browsers */
    url("/src/fonts/Roboto/roboto-v20-latin_cyrillic-regular.ttf") format("truetype"),
    /* Safari, Android, iOS */
    url("/src/fonts/Roboto/roboto-v20-latin_cyrillic-regular.svg#Roboto") format("svg");
  /* Legacy iOS */
}

